<template>
  <div class="member-invite">
    <Tabs value="">
      <TabPane label="" name="name1"></TabPane>
      <TabPane label="" name="name2">
        <div class="url-way">
          <span class="url-way-tip"></span>
          <Input v-model="inviteUrl" size="large" ref="inviteUrlInput" disabled>
          <span slot="prepend"></span>
          <Button slot="append" @click="coverUrl" class="cover-url-btn" v-show="!isCover"></Button>
          <Button slot="append" @click="coverUrl" class="cover-url-btn" v-show="isCover"></Button>
          </Input>
        </div>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        inviteUrl: 'https://www.tracup.com/user/invite/39eeedc6a9946217595204d64b6589b0',
        isCover: false
      }
    },
    methods: {
      coverUrl () {
        // 
        this.$refs.inviteUrlInput.$refs.input.select()
        document.execCommand('copy')
        this.$Notice.success({
          title: ''
        })
        if (!this.isCover) {
          this.isCover = true
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped type="text/stylus" lang="stylus">
  @import "../../../../style/base.styl"
  .member-invite
    .url-way
      height 100%
      padding 20px
      display flex
      flex-direction column
      justify-content center
      align-items flex-start
      .url-way-tip
        margin-bottom 10px
        color $-sub-color
        font-size 14px
</style>



