/**
 * mock data
 * 
 */
const _statusList = [
  {status: 0, label: '', icon: 'social-twitter', color: '#45be95'},
  {status: 1, label: '', icon: 'social-facebook', color: '#ffbc00'},
  {status: 2, label: '', icon: 'social-github', color: '#5bc0de'},
  {status: 3, label: '', icon: 'social-snapchat', color: '#a2d148'},
  {status: 4, label: '', icon: 'social-snapchat', color: '#8b7cc5'},
  {status: 5, label: '', icon: 'social-snapchat', color: '#bedad3'},
  {status: 6, label: '', icon: 'social-snapchat', color: '#f1494e'}
]

/**
 * mock data
 * 
 *
 */
const _question = {
  title: '',
  author: 'zhoulin',
  summary: '',
  assign: '',
  id: '#001',
  assigner: '',
  status: 1,
  level: 1,
  category: 1,
  updateAt: '2017-12-29 22:35',
  createAt: '2017-12-29 22:35'
}

/**
 * mock data
 * 
 */
const _questionCategoryList = [
  {
    status: 0,
    label: '',
    icon: 'record',
    color: 'blue'
  },
  {
    status: 1,
    label: 'bug',
    icon: 'record',
    color: 'green'
  },
  {
    status: 2,
    label: '',
    icon: 'record',
    color: 'red'
  }
]
/**
 * 
 * @type {[null,null,null]}
 */
const _questionLevels = [
  {
    status: 0,
    label: '',
    icon: 'record',
    color: '#5bc0de'
  },
  {
    status: 1,
    label: '',
    icon: 'record',
    color: '#ffbc00'
  },
  {
    status: 2,
    label: '',
    icon: 'record',
    color: '#45be95'
  }
]

export default {
  getStatusList (cb) {
    setTimeout(() => cb(_statusList), 1000)
  },
  getQuestion (cb) {
    setTimeout(() => {
      cb(_question)
    }, 1000)
  },
  getQuestionLevels (cb) {
    setTimeout(() => cb(_questionCategoryList), 2000)
  },
  getQuestionCategories (cb) {
    setTimeout(() => {
      cb(_questionLevels)
    }, 100)
  }
}



